  /* Add a black background color to the top navigation */
.topnav {
    background-color: #333;
    overflow: hidden;
    background-image: url('https://c4.wallpaperflare.com/wallpaper/65/991/687/minimalism-textured-digital-art-black-wallpaper-preview.jpg');
    border-radius: 4px;
    margin: 5px;
    box-shadow: 1px 1px 1px rgb(106, 106, 107), 0 0 15px rgb(255, 255, 255);

  }
  
  /* Style the links inside the navigation bar */
  .topnav a {
    float: left;
    color: #f2f2f2;
    text-align: center;
    padding: 10px 12px;
    text-decoration: none;
    font-size: 17px;
  }
  
  /* Change the color of links on hover */
  .topnav a:hover {
    background-color: #ddd;
    color: black;
  }
  
  /* Add a color to the active/current link */
  .topnav a.active {
    background-color: #04AA6D;
    color: white;
  }

